<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
    <DeptoBase
    :goal="rotulado.meta_semana"
    :progress="rotulado.progress"
    :name="rotulado.nombre"
    :depto="rotulado.depto"
    :days="rotulado.dias_laborados"
    :m3="rotulado.m3_persona"
    :bonus_p="rotulado.bono_persona"
    :bonus_tot="rotulado.bono_depto"
    :asis="rotulado.asistencia"
    :extra="rotulado.$_extra_m3"
    :bonus_prod="rotulado.bono_productividad"
    :bonus_goals="rotulado.bono_metas"
    />
</div>
</template>


<script>

import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Rotulado',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'rotulado',
            userCode: this.$route.params.user,
            city: 'hermosillo'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        rotulado() {
        return this.$store.getters["plants/data"];
        }
    }
}

</script>